<template>
	<div id="printer" class="warp">
		<!--头部导航 start-->
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/shop' }">店铺管理</el-breadcrumb-item>
				<el-breadcrumb-item :to="{ path:'/shop/printer'}">打印机管理</el-breadcrumb-item>
				<el-breadcrumb-item>编辑打印机</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<!--头部导航 end-->
		<!--编辑打印机 start-->
		<div class="printer-info">
			<div class="title">
				编辑打印机
			</div>
			<div class="info-warp">
				<el-form ref="form" label-width="120px" :model='form'>
					<el-form-item label="打印机ID">
						<span>{{printerID}}</span>
					</el-form-item>
					<el-form-item class='star' label="名称">
						<el-input @change='name' v-model='form.printer_name'></el-input>
						<span class="prompting">注：名称必须和控制面板中的打印机名称一字不差</span>
					</el-form-item>
					<el-form-item class='star' label="规格">
						<el-radio-group @change='spec' v-model="form.printer_spec">
							<el-radio label=58>58mm</el-radio>
							<el-radio label=80>80mm</el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item   label='支持单据类型' class='doctype'>
						<el-checkbox-group @change='doctype' v-model="form.type">
							<el-checkbox :label='documentsType.MENUSAFTERKITCHEN' name='type'>{{documentsType.toString(documentsType.MENUSAFTERKITCHEN)}}</el-checkbox>
							<el-checkbox :label='documentsType.INVALIDSINGLE' name='type'>{{documentsType.toString(documentsType.INVALIDSINGLE)}}</el-checkbox>
							<el-checkbox :label='documentsType.MENUCONSUMER' name='type'>{{documentsType.toString(documentsType.MENUCONSUMER)}}</el-checkbox>
							<el-checkbox :label='documentsType.STATEMENT' name='type'>{{documentsType.toString(documentsType.STATEMENT)}}</el-checkbox>
							<el-checkbox :label='documentsType.PRESTATEMENT' name='type'>{{documentsType.toString(documentsType.PRESTATEMENT)}}</el-checkbox>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='goods' label='后厨单打印分类'>
						<div class="classion" v-for='(item,index) in classionf' :key='index'>
							<el-select v-model='item.value1' placeholder="请选择" @change='change(index,0)'>
								<el-option v-for='items in item.options1' :key="items.idx" :label="items.label" :value="items.idx">
								</el-option>
							</el-select>
							<el-select v-model='item.value2' placeholder="请选择" @change='change(index,1)'>
								<el-option v-for='items in item.options2' :key='items.idx' :label="items.label" :value="items.idx">
								</el-option>
							</el-select>
							<el-select v-model='item.value3' placeholder="请选择" @change='change(index,2)'>
								<el-option v-for='items in item.options3' :key='items.idx' :label="items.label" :value="items.idx">
								</el-option>
							</el-select>
							<span class="adds" @click="addcls(index)" v-if='index==0'>添加所属分类</span>
							<span class="del" @click="delcls(index)" v-if='index>0'></span>
						</div>
					</el-form-item>
					<el-form-item label="型号">
						<el-input v-model='form.printer_model'></el-input>
					</el-form-item>
					<el-form-item label="备注">
						<el-input type='textarea' :maxlength=60 v-model='form.remark' placeholder='备注文字总数不得超过120字'></el-input>
					</el-form-item>
				</el-form>
			</div>
		</div>
		<!--编辑打印机 end-->
		<!--打印位置调整 start-->
		<div class="hei_seting">
			<div class="title">
				高级设置
			</div>
			<div class="seting-positon">
				<span class="text">打印位置调整 </span><span class="lp"> Left </span> <input type="text" v-model="form.left" class="ipt" /> mm <span class="tp">Top</span> <input class="ipt" type="text" v-model="form.top" /> mm
			</div>
			<div class="btn">
				<button type="button" ref='btn' :class="{blue: showBtn}" class="gey" @click="onSubmit">保存</button>
				<button type="button" class="default" @click='goback'>取消</button>
			</div>
		</div>
		<!--打印位置调整 end-->
		<!--保存成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--保存成功失败 Tips end-->
	</div>
</template>

<script>
	import { documentsType } from '@/config/cfg';
	import { getCategory, getPrinter, savePrinter, getPrintID } from '../../api';
	import { Comethods } from '../../index';
	export default {
		data() {
			return {
				form: {
					printer_name: '', //打印机名称
					printer_spec: '', //打印机规格
					printer_model: '', //打印机型号
					remark: '', //备注
					left: null, //打印位置
					top: null, //打印位置
					type: [] //支持单据类型
				},
				classionf: [], //后厨打印分类数组
				initList: { //初始化对象
					value1: '',
					value2: '',
					value3: '',
					options1: [],
					options2: [],
					options3: []
				},
				list: [], //后台返回数据
				category: [], //存放选中打印分类ID
				cateID: [], //选中商品的ID
				printerID: null, //获取后台返回的ID
				printersID: null, //获取编辑传过来的ID
				printer_info: null, //后台返回数据（打印分类选择的对象）
				categor: [], //后台返回打印分类选择
				showBtn:false, //给按钮加上class样式
				showTips: false,//控制显示Tips提示框
				msg:''//提示框提示内容
			}
		},
		created() {
			this.getData();
			if(!this.printersID) {
				this.getList();    //如果不是编辑传过来的ID，获取菜品分类数据
			}
		},
		computed: {
			documentsType() {
				return documentsType;
			}
		},
		methods: {
			//保存编辑内容
			onSubmit() {
				let data = {
					save_printer: 1,
					printer_id: this.printerID,
					printer_name: this.form.printer_name,
					printer_size: this.form.printer_spec,
					printer_category: this.form.type,
					printer_brand: this.form.printer_model,
					printer_note: this.form.remark,
					print_position_left: this.form.left,
					print_position_top: this.form.top,
					food_category_list: this.category
				};
				savePrinter(data, this.alts);
			},
			//保存后的回调
			alts(resp){
				if(resp.ret === 0){
					this.showTips = true;
					this.msg = '保存成功';
					setTimeout(()=>{
						this.$router.go(-1);
					},1500);
				}else{
					this.showTips = true;
					this.msg = '保存失败';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
				}
			},
			//取消按钮跳转
			goback() {
				this.$router.go(-1);
			},
			//添加按钮功能
			addcls(index) {
				let lists = JSON.parse(JSON.stringify(this.initList));
				this.classionf.push(lists);
			},
			//删除图标功能
			delcls(index) {
				this.classionf.splice(index, 1);
				this.category.splice(index, 1);
			},
			//初始化数据
			initData() {
				let list = this.list,
					categor = this.categor;
				this.initList.options1 = this.getOptions(list);
				if(categor.length == 0) {
					this.addcls(-1);
				}
				categor.forEach((item, index) => {
					let obj = {
						value1: '',
						value2: '',
						value3: '',
						options1: this.getOptions(list),
						options2: [],
						options3: []
					};
					try {
						if(!item[0].category_id) {
							obj.value1 = '全部';
							obj.value2 = '全部';
							obj.value3 = '全部';
						}
						if(item[0].category_id != 0) {
							obj.value1 = '全部';
							obj.value2 = '全部';
							obj.value3 = '全部';
						}
						if(item[1].category_id) {
							obj.value3 = '全部';
						}
					} catch(err) {}

					try {
						list.forEach((itm, idx) => {
							if(itm.category_id == item[0].category_id) {
								obj.value1 = idx;
								obj.options2 = this.getOptions(list[obj.value1].list)
							}
						});
					} catch(err) {}
					try {
						list[obj.value1].list.forEach((itm, idx) => {
							if(itm.category_id == item[1].category_id) {
								obj.value2 = idx;
								obj.options3 = this.getOptions(list[obj.value1].list[obj.value2].list)
							}
						})
					} catch(err) {}
					try {
						list[obj.value1].list[obj.value2].list.forEach((itm, idx) => {
							if(itm.category_id == item[2].category_id) {
								obj.value3 = idx;
							}
						})
					} catch(err) {}
					this.classionf.push(obj);
				});

			},
			//获取菜品分类列表
			getData() {

				let _this = this;
				if(this.$route.query.id != undefined) {
					this.printersID = this.$route.query.id;
					getPrinter({
						printer_info: 1,
						printer_id: this.printersID,
					}, function(resp) {
						_this.printer_info = resp.data.info;
						_this.categor = _this.printer_info.category;
						if(_this.printer_info.printer_id == _this.printersID) {
							_this.form.printer_name = _this.printer_info.printer_name;
							_this.form.printer_spec = String(_this.printer_info.printer_size);
							_this.form.printer_model = _this.printer_info.printer_brand;
							_this.form.remark = _this.printer_info.printer_note;
							_this.printerID = _this.printer_info.printer_id;
							_this.category = _this.printer_info.food_category_list;
							_this.form.type = _this.printer_info.printer_category || [];
							_this.form.left = _this.printer_info.print_position_left;
							_this.form.top = _this.printer_info.print_position_top;
						}
						_this.getList();
					});
				} else {
					this.getPrintIDs();
					this.printersID = this.printerID;
				}
			},
			//获取菜品分类数据
			getList() {
				let _this = this;
				getCategory({
					list: 1,
					type: 1
				}, function(resp) {
					_this.list = resp.data.list || [];
					let all = {
						category_name: '全部',
						category_id: '0'
					};
					let secall = {
						category_name: '全部',
						category_id: '1'
					};
					let thrall = {
						category_name: '全部',
						category_id: '2'
					};
					try {
						_this.list.push(all);
						_this.list.forEach(item => {
							if(item.list) {
								item.list.push(secall);
							}
							item.list.forEach(items => {
								if(items.list) {
									items.list.push(thrall);
								}
							});
						});
					} catch(err) {}
					_this.initData();
					_this.showButton();
				});
			},
			//select选中事件
			change(val, n) {
				let list = this.classionf[val];
				if(n == 0) {
					list.value2 = '';
					list.value3 = '';
					list.options2 = [];
					list.options3 = [];
				}
				if(n == 1) {
					list.value3 = '';
					list.options3 = [];
				}
				let {
					value1,
					value2
				} = list;

				try {

					this.cateID[val] = list['options' + (n + 1)][list['value' + (n + 1)]].id;
					if(this.cateID[val] == 1 || this.cateID[val] == 2) {
						this.category[val] = list['options' + (n)][list['value' + (n)]].id;
					} else {
						this.category[val] = list['options' + (n + 1)][list['value' + (n + 1)]].id;
					}
					let list1 = this.list[value1].list;
					list.options2 = this.getOptions(list1);
					let list2 = this.list[value1].list[value2].list;
					list.options3 = this.getOptions(list2);
				} catch(err) {}
			},
			//格式化数据
			getOptions(arr) {
				return arr.map((item, index) => {
					return {
						idx: index,
						label: item.category_name,
						id: item.category_id
					}
				});
			},
			//创建ID
			getPrintIDs() {
				let data = {
					genid: 1,
					type: "printer"
				};
				getPrintID(data, resp => {
					if(resp.ret === 0) {
						this.printerID = resp.data.id;
					} else {
						this.showTips = true;
						this.msg = '获取ID失败';
						setTimeout(()=>{
							this.showTips = false;
						},1500);
					}
				});
			},
			//显示按钮激活样式
			showButton() {
				if(this.printerID && this.form.printer_name !== '' && this.form.printer_spec !== '' && this.form.type.length !== 0)
				 {
					this.showBtn = true;
					let btn = this.$refs.btn;
					btn.disabled = false;
				} else {
					this.showBtn = false;
					let btn = this.$refs.btn;
					btn.disabled = true;
				}
			},
			//change事件
			name(){
				this.showButton();
			},
			spec(){
				this.showButton();
			},
			doctype(){
				this.showButton();
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		background: #fff;
		.nvs {
			background: #EFF3FA;
			height: 26px;
		}
		.tips {
			position:fixed;
			z-index: 1003;
			@include wh(360px, 100px);
			background: #000;
			opacity: 0.8;
			left: 50%;
			top: 50%;
			margin-left: -180px;
			margin-top: -50px;
			text-align: center;
			border-radius: 2px;
			.info {
				display: inline-block;
				@include fc(14px, #fff);
				padding-top: 40px;
				letter-spacing: 0.5;
			}
		}
		button {
			cursor: pointer;
		}
		.ipt {
			text-align: center;
		}
		.printer-info {
			.title {
				@include fc($sizes, $grey);
				height: 40px;
				line-height: 40px;
				background: #F6F8FC;
				text-indent: 14px;
			}
			.info-warp {
				padding-top: 40px;
				margin-left: 128px;
				.prompting {
					position: absolute;
					left: 300px;
					top: 10px;
					@include fc(12px, #9b9b9b);
				}
			}
			.adds {
				position: relative;
				cursor: pointer;
				display: inline-block;
				@include wh(150px, 36px);
				border: 1px solid #4877E7;
				@include fc(14px, #4877E7);
				padding-left: 45px;
				&:before {
					position: absolute;
					content: '';
					left: 24px;
					top: 10px;
					background: url(../img/add1.png)no-repeat;
					@include wh(16px, 16px);
				}
			}
			.del {
				position: relative;
				cursor: pointer;
				&:before {
					position: absolute;
					content: '';
					left: 0;
					top: 0;
					background: url(../img/del.png)no-repeat;
					@include wh(24px, 24px);
				}
			}
		}
		.hei_seting {
			height: 204px;
			padding: 0 0 50px 0;
			.title {
				@include fc($sizes, $grey);
				height: 40px;
				line-height: 40px;
				background: #F6F8FC;
				text-indent: 14px;
			}
			.seting-positon {
				margin: 40px 0 40px 142px;
				.text {
					@include fc(14px, #000);
					opacity: 0.57;
				}
				.lp {
					margin-left: 14px;
					color: #333;
				}
				.tp {
					margin-left: 56px;
					color: #333;
				}
				input {
					@include wh(50px, 34px);
					border: 1px solid #d9d9d9;
				}
			}
			.btn {
				text-align: center;
				padding-bottom: 20px;
				background: #fff;
				.gey {
					@include fc(16px, #ffffff);
					background: #f2f2f2;
					@include wh(160px, 40px);
					line-height: 40px;
					border-radius: 6px;
				}
				.default {
					@include fc(14px, #4877E7);
					background: #ffffff;
					@include wh(160px, 40px);
					line-height: 40px;
					border-radius: 6px;
					border: 1px solid #4877E7;
					margin-left: 50px;
				}
			}
			
			.blue{
				background: #4877E7 !important;
			}
		}
	}
</style>
<style lang="scss">
	@import 'src/styles/mixin.scss';
	#printer {
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover {
			color: #4877E7;
		}
		.el-input__inner {
			border: 1px solid #d9d9d9;
			border-radius: 0;
			@include wh(280px, 34px);
		}
		.goods {
			max-width: 860px;
			position: relative;
		}
		.goods input {
			border: 1px solid #d9d9d9;
			border-radius: 0;
			@include wh(180px, 34px);
		}
		.el-select {
			margin-right: 8px;
			margin-bottom: 8px;
		}
		.el-textarea__inner {
			@include wh(400px, 180px);
			resize: none;
			border-radius: 0;
			border: 1px solid #D9D9D9;
		}
		.el-select .el-input .el-input__icon {
			background: #f2f2f2;
		}
		.el-form-item__label {
			color: #000;
			opacity: 0.57;
		}
		.error {
			@include fc(12px, #ff4949);
		}
		.el-select .el-input .el-input__icon {
			background: 0;
		}
		.el-checkbox+.el-checkbox{
			margin-left: 50px;
		}
		.star{
			position: relative;
			&:before{
				content: '*';
			    left:70px;
			    top: 14px;
			    position: absolute;
			    display: inline-block;
			    width: 10px;
			    height: 10px;
			    color: #E7487E;
			}
		}
		.doctype{
			position: relative;
			&:before{
				content: '*';
			    left:14px;
			    top: 14px;
			    position: absolute;
			    display: inline-block;
			    width: 10px;
			    height: 10px;
			    color: #E7487E;
			}
		}
	}
</style>


